<template>
    <div v-if="loading" style="height: 100%;">
        <v-container fill-height fluid>
            <v-row align="center" justify="center">
                <v-col md="4" class="d-flex justify-center">
                    <v-progress-circular
                        :size="70"
                        :width="7"
                        color="#c41e61"
                        indeterminate
                    ></v-progress-circular>
                </v-col>
            </v-row>
        </v-container>
    </div>
    <div v-else>
        <v-container fluid>
            <v-row>
                <!-- Left Column -->
                <v-col cols="8" class="pt-0">
                    <!-- Sensor Controls -->
                    <v-col cols="12" class="pt-0">
                    <div class="top-container">
                        <v-container>
                            <v-row class="pt-2">
                                <v-col cols="3" class="pa-0">
                                    <v-row class="align-center">
                                        <v-col>
                                            <div class="toggle-title">FAN</div>
                                        </v-col>
                                        <v-col>
                                            <v-switch @change="toggleSwitch" color="#c41e61"
                                                v-model="fanActive" :disabled="modeActive"
                                            ></v-switch>
                                        </v-col>
                                    </v-row>
                                </v-col>
                                <v-col cols="3" class="pa-0">
                                    <v-row class="align-center">
                                        <v-col>
                                            <div class="toggle-title">LIGHT</div>
                                        </v-col>
                                        <v-col>
                                            <v-switch @change="toggleSwitch" color="#c41e61"
                                                v-model="lightActive" :disabled="modeActive"
                                            ></v-switch>
                                        </v-col>
                                    </v-row>
                                </v-col>
                                <v-col cols="3" class="pa-0">
                                    <v-row class="align-center">
                                        <v-col>
                                            <div class="toggle-title">PUMP</div>
                                        </v-col>
                                        <v-col>
                                        <v-switch @change="toggleSwitch" color="#c41e61"
                                                v-model="pumpActive" :disabled="modeActive"
                                            ></v-switch>
                                        </v-col>
                                    </v-row>
                                </v-col>
                                <v-col cols="3" class="pa-0">
                                    <v-row class="align-center">
                                        <v-col>
                                            <div class="toggle-title">AUTO</div>
                                        </v-col>
                                        <v-col>
                                            <v-switch @change="toggleSwitch" color="#c41e61"
                                                v-model="modeActive"
                                            ></v-switch>
                                        </v-col>
                                    </v-row>
                                </v-col>
                            </v-row>
                        </v-container>
                    </div>
                    </v-col>
                    
                    <!-- Metrics -->
                    <v-col cols="12">
                        <v-row justify="start">
                            <!-- PH Level  -->
                            <template v-for="card in cardData">
                                <v-col cols="6" :key="card.id">
                                    <div class="pa-2 fill-height card-container"> 
                                        <!-- top row -->
                                        <v-container  class="fill-height align-lg-stretch">
                                            
                                            <v-row justify="space-between" style="align-items: center;">
                                                <v-col md="10">
                                                    <span class="card-name">
                                                        {{card.name}}
                                                    </span>
                                                </v-col>
                                                <v-col md="2" style="text-align: right;">
                                                    <div class="meets--text active-circle" :class="{active: card.meetsOptimalValue}"></div>
                                                </v-col>
                                            </v-row>
                                        <!-- middle row  -->
                                            <v-row style="align-items: center;">
                                                <!-- Value  -->
                                                <v-col cols="4">
                                                    <div class="card-value">
                                                        {{card.value}}{{ (card.scaleType === 'degrees') ? `&#176;` : null }}{{ (card.dataType === 'percentage') ? `&#37;` : null }}
                                                    </div>
                                                </v-col>
                                                <!-- progress bar  -->
                                                <v-col cols="8">
                                                    <!-- bar  -->
                                                    <v-progress-linear
                                                    class="progress"
                                                    color="green"
                                                    :value="card.percentage"
                                                    ></v-progress-linear>
                                                    <!-- legend  -->
                                                </v-col>
                                            </v-row>
                                            <v-row>
                                                <v-col cols="4">
                                                    <div>
                                                        &nbsp;
                                                    </div>
                                                </v-col>
                                                
                                                <v-col cols="8" class="pt-0">
                                                    <div class="-text" :style="{ backgroundPosition: card.optimalPercentage + '%'}"></div>
                                                    <!-- Blocks  -->
                                                    <div v-if="card.scaleType === 'blocks'" id="blocks-meter-bar" class="d-flex flex-row justify-space-around">
                                                        <div class="square-meter-value one">1</div>
                                                        <div class="square-meter-value two">2</div>
                                                        <div class="square-meter-value three">3</div>
                                                        <div class="square-meter-value four">4</div>
                                                        <div class="square-meter-value five">5</div>
                                                        <div class="square-meter-value six">6</div>
                                                        <div class="square-meter-value seven">7</div>
                                                        <div class="square-meter-value eight">8</div>
                                                        <div class="square-meter-value nine">9</div>
                                                        <div class="square-meter-value ten">10</div>
                                                        <div class="square-meter-value eleven">+11</div>
                                                    </div>

                                                    <!-- Degrees Gradient  -->
                                                    <div v-if="card.scaleType === 'degrees'" id="degrees-meter-bar" class="d-flex flex-row justify-space-between pa-1">
                                                        <div class="degrees-low">0</div>
                                                        <div class="degrees-high">100&#xb0;</div>
                                                    </div>

                                                    <!-- range meter  -->

                                                    <div v-if="card.scaleType === 'range'" id="range-meter-bar" class="d-flex flex-row align-center">
                                                        <div class="range-one" style="width: 22%;">-200</div>
                                                        <div class="range-two" style="width: 28%;">200-400</div>
                                                        <div class="range-three" style="width: 28%;">400-800</div>
                                                        <div class="range-four" style="width: 22%;">+800</div>
                                                    </div>

                                                    <div v-if="card.scaleType === 'gradient'" id="gradient-meter" class="d-flex flex-row justify-space-between pa-1">
                                                        <div class="gradient-low">0</div>
                                                        <div class="gradient-high">100%</div>
                                                    </div>
                                                    <div class="optimal-value-text">
                                                        {{ card.optimalValue}}{{ (card.scaleType === 'degrees') ? `&#176;` : null }}{{ (card.dataType === 'percentage') ? `&#37;` : null }} optimal
                                                    </div>
                                                </v-col>

                                            </v-row>
                                        </v-container>
                                    </div>
                                </v-col>
                                
                            </template>
                        </v-row>
                    </v-col>                 
                </v-col>

                <!-- Column 2 -->
                <v-col cols="4">
                    <!-- days to harvest section  -->
                    <div class="card-container pr-6 harvest-box">
                        <v-row class="pa-10">
                            <v-col cols="8">
                                <div outlined tile style="height: 100%;">
                                    <v-card class="pa-5 harvest-card">
                                        <v-row>
                                            <v-col cols="4">
                                                <v-img class="leaf-logo" src="../assets/LeafLogo.png" contain></v-img>

                                            </v-col>
                                        </v-row> 
                                        <v-row justify="center">
                                            <v-col cols="12" style="text-align: center;">
                                                <span class="harvest-value">
                                                    <h1>{{ this.harvestTime }}</h1>
                                                </span>

                                            </v-col>
                                        </v-row> 
                                        <v-row justify="center">
                                            <v-col cols="12">
                                                <hr class="harvest-border">
                                                <div class="harvest-title" style="text-align: center;">DAYS TO HARVEST</div>
                                            </v-col>
                                        </v-row> 
                                    </v-card>
                                </div>
                                <!-- <v-card class="pa-2" outlined tile> -->
                                <!-- </v-card> -->
                            </v-col>
                            <v-col cols="4" class="latency-uplink-col">
                                <div class="pa-2 small-container mt-4">
                                    <v-row>
                                        <div class="pt-3 pl-3 small-container-title">
                                            Latency
                                        </div>
                                    </v-row> 
                                    <v-row justify="center" class="mt-2">
                                        <v-col cols="12" style="text-align: center;">
                                            <h1 id="uplink-value">{{ this.latency }}</h1>
                                        </v-col>
                                    </v-row> 
                                    <v-row justify="center" class="mt-0">
                                        <v-col cols="4" style="text-align: center;">
                                            Ms

                                        </v-col>
                                    </v-row>
                                </div>
                                <div class="pa-2 small-container mt-4">
                                    <v-row>
                                        <div class="pt-3 pl-3 small-container-title">
                                            Uplink
                                        </div>
                                    </v-row> 
                                    <v-row justify="center" class="mt-2">
                                        <v-col cols="12" style="text-align: center;">
                                            <h1 id="uplink-value">{{ this.uplink }}</h1>
                                        </v-col>
                                    </v-row> 
                                    <v-row justify="center" class="mt-0">
                                        <v-col cols="4" style="text-align: center;">
                                            MB/s

                                        </v-col>
                                    </v-row> 
                                </div>
                            </v-col>
                        </v-row>
                        <!-- Live Feed -->
                        <!-- Harvest and Uplink Metrics -->
                        <v-spacer></v-spacer>
                    </div>
                    <div class="card-container mt-16 live-feed-box">
                        <v-card-text>
                            <div class="active-circle"></div>
                            <span class="live-feed pl-1">LIVE FEED</span>
                        </v-card-text>
                        <v-container>
                            <v-row>
                                <v-col id="live-feed-video">
                                    <v-img class="preview" src="https://vm2.techexsar.xyz/hydroponics" lazy-src="../assets/preview.png" />
                                </v-col>
                            </v-row>
                        </v-container>
                    </div>                  
                    
                </v-col>
            </v-row>
        </v-container>
    </div>
</template>

<script>
export default {
    data: () => ({
        loading: true,
        gradientValue: 75,
        bufferValue:100,
        gradient: `linear-gradient(180.03deg, #E20074 -28.53%, #C200E2 99.97%);`,
        sensorData: {},
        fanActive: false,
        lightActive: true,
        pumpActive: false,
        heaterActive: false,
        modeActive: false,
        latency: 0,
        uplink: 0,
        harvestTime: 0
    }),
    async mounted() {
        this.$nextTick(function () {
            window.setInterval(() => {
                this.getData();
            }, 5000);
        })
    },    
    async created() {
        await this.getData();
        await this.setToAuto();
        this.loading = false;
    },
    computed: {
        cardData() {
            return [
                {
                    name: 'PH LEVEL',
                    id: 1,
                    value: Math.round(this.sensorData.tank_pH * 10) / 10 || 7,
                    // between 6-9...
                    meetsOptimalValue: (Math.round(this.sensorData.tank_pH * 10) / 10 >= 6 && Math.round(this.sensorData.tank_pH * 10) / 10 <= 9),
                    dataType: 'number', //number, degree, percentage
                    progressValue: 5,
                    scaleMax: 11,
                    scaleType: 'blocks', //blocks, gradient,
                    // the value divided by scale max times 100
                    percentage: ((Math.round(this.sensorData.tank_pH * 10) / 10) / 11) * 100 || (7 / 11) * 100,
                    optimalValue: '5.3',
                    optimalPercentage: 5.3 / 11 * 100
                },
                {
                    name: 'RESERVOIR TEMPERATURE',
                    id: 2,
                    value: Math.round(this.sensorData.tank_temp) || 60,
                    // between 65-80...
                    meetsOptimalValue: (Math.round(this.sensorData.tank_temp) >= 65 && Math.round(this.sensorData.tank_temp) <= 80),
                    dataType: 'number', //number, degree, percentage
                    progressValue: 50,
                    scaleMax: 100,
                    scaleType: 'degrees', //blocks, gradient
                    optimalValue: 69,
                    percentage: ((Math.round(this.sensorData.tank_temp * 10) / 10) / 100) * 100 || (50 / 100) * 100,
                    optimalPercentage: 69
                },
                {
                    name: 'NUTRIENT LEVEL',
                    id: 3,
                    value: Math.round(this.sensorData.tank_waterlevel) || 30,
                    // Optimal value still unknown, setting to true always for now.
                    meetsOptimalValue: true,
                    dataType: 'number', //number, degree, percentage
                    scaleMax: 800,
                    scaleType: 'range',
                    percentage: (Math.round(this.sensorData.tank_waterlevel) / 800) * 100 || (30 / 800) * 100,
                    optimalValue: '520-580',
                    optimalPercentage: 550 / 800 * 100
                },
                {
                    name: 'AMBIENT TEMP',
                    id: 4,
                    value: Math.round(this.sensorData.ambient_temperature) || 22,
                    // between 60-75...
                    meetsOptimalValue: (Math.round(this.sensorData.ambient_temperature) >= 60 && Math.round(this.sensorData.ambient_temperature) <= 75),
                    dataType: 'number', //number, degree, percentage
                    scaleMax: 100,
                    scaleType: 'degrees', //blocks, gradient
                    optimalValue: '72',
                    percentage: 3,
                    optimalPercentage: 72
                },
                {
                    name: 'HUMIDITY',
                    id: 5,
                    value: Math.round(this.sensorData.ambient_humidity) || 75,
                    // between 70-80...
                    meetsOptimalValue: (Math.round(this.sensorData.ambient_humidity) >= 70 && Math.round(this.sensorData.ambient_humidity) <= 80),
                    dataType: 'percentage', //number, degree, percentage
                    scaleMax: 100,
                    scaleType: 'gradient', //blocks, gradient
                    optimalValue: 40,
                    percentage: ((Math.round(this.sensorData.ambient_humidity * 10) / 10) / 100) * 100 || (50 / 100) * 100,
                    optimalPercentage: 40
                },
                {
                    name: 'FLOW RATE',
                    id: 6,
                    value: Math.round(this.sensorData.tank_waterlevel * 10) / 1000 || 30,
                    // between 0.26-0.52...
                    meetsOptimalValue: (Math.round(this.sensorData.tank_waterlevel * 10) / 10 >= 0.26 && Math.round(this.sensorData.tank_waterlevel * 10) / 10 <= 0.52),
                    dataType: 'number', //number, degree, percentage
                    scaleMax: 1,
                    scaleType: 'gradient', //blocks, gradient
                    optimalValue: 0.25,
                    percentage: ((Math.round(this.sensorData.ambient_humidity * 10) / 10) / .5)
                }
            ]
        },        
    },
    methods: {
        callAPI(method, endpoint, postBody) {
            return new Promise((resolve, reject) => {
                const API_URL = document.location.href;

                const headers = {
                    headers: {
                        'Content-Type': 'application/json; charset=utf-8'
                    }
                };

                this.$http[method](`${API_URL.split('#')[0]}${endpoint}`, postBody, headers)
                .then((res) => {
                    if (res.status === 200) {
                        resolve(res);
                    } else {
                        console.log('err 1');
                        reject(res);
                    }
                })
                .catch((err) => {
                    console.log('err 2', err);
                    reject(err);
                });
            });
        },
        getData() {
            return new Promise((resolve, reject) => {
                this.callAPI('get', 'api/getdata')
                .then((response) => {
                    this.harvestTime = response.data.harvestTime
                    this.sensorData = response.data.sensorData;
                    const toggleData = response.data.toggleData;
                    const networkSpeed = response.data.networkSpeed;

                    // Set toggles with info from database.
                    this.fanActive = !!toggleData['fan'];
                    this.pumpActive = !!toggleData['pump'];
                    this.heaterActive = !!toggleData['heater'];
                    this.lightActive = !!toggleData['light'];
                    this.modeActive = !!toggleData['mode'];
                    this.uplink = Math.round(networkSpeed.uplink);
                    this.latency = Math.round(networkSpeed.latency);

                    resolve();
                })
                .catch((err) => {
                    console.log('error')
                    reject(err);
                });
            });            
        },
        toggleSwitch() {
            return new Promise((resolve, reject) => {
                this.callAPI('post', 'api/togglesensor', {
                    Inputs: {
                        heater: +this.heaterActive,
                        pump: +this.pumpActive,
                        fan: +this.fanActive,
                        light: +this.lightActive,
                        mode: +this.modeActive
                    }
                })
                .then(() => {
                    resolve();
                })
                .catch((err) => {
                    console.log('error')
                    reject(err);
                });
            });            
        },
        setToAuto() {
            return new Promise((resolve, reject) => {
                this.callAPI('post', 'api/togglesensor', {
                    Inputs: {
                        heater: +this.heaterActive,
                        pump: +this.pumpActive,
                        fan: +this.fanActive,
                        light: +this.lightActive,
                        mode: 1
                    }
                })
                .then(() => {
                    this.modeActive = true; // Always set to auto mode on load.
                    resolve();
                })
                .catch((err) => {
                    console.log('error')
                    reject(err);
                });
            });            
        }          
    },
};
</script>

<style>

@font-face {
  font-family: "TeleNeo";
  src: local("TeleNeo"),
   url(../assets/fonts/TeleNeo-Regular.otf) format("opentype");
   font-weight: 400;
}
@font-face {
  font-family: "TeleNeo";
  src: local("TeleNeo"),
   url(../assets/fonts/TeleNeo-Medium.otf) format("opentype");
   font-weight: 500;
}
@font-face {
  font-family: "TeleNeo";
  src: local("TeleNeo"),
   url(../assets/fonts/TeleNeo-Bold.otf) format("opentype");
   font-weight: 700;
}
@font-face {
  font-family: "TeleNeo";
  src: local("TeleNeo"),
   url(../assets/fonts/TeleNeo-ExtraBold.otf) format("opentype");
   font-weight: 800;
}

* {
    font-family: Teleneo !important;
    font-weight: 400;
    font-size: 16px;
}

.leaf-logo {
    max-height: 25px !important;
    max-width: 90px !important;
    margin-left: -38px;
    margin-top: -5px;
}

.card-container {
    background: #FFFFFF;
    box-shadow: 10px 10px 30px rgba(174, 174, 192, 0.4), -10px -10px 30px #FFFFFF;
    border-radius: 3px;  
}

.top-container {
    background: #FFFFFF;
    box-shadow: 10px 10px 30px rgba(174, 174, 192, 0.4), -10px -10px 30px #FFFFFF;
    height: 80px;
}

.latency-uplink-col {
    margin-top: -12px;
}

.small-container {
    background: #FFFFFF;
    box-shadow: 10px 10px 30px rgba(174, 174, 192, 0.4), -10px -10px 30px #FFFFFF;
    border-radius: 6px;
    position: relative;
    width: 135px;
    height: 135px;
    overflow: hidden;    
}

.small-container-title{
    font-size: 1.1rem !important;
    font-weight: 500 !important;
}
.v-content {
    font-size: 0.8rem !important;
}
.v-application .green{
    background: linear-gradient(180.03deg, #E20074 -28.53%, #C200E2 99.97%) !important;
}

.v-progress-linear {
    background: #E5E5E5;
    height: 3.3rem !important;
    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.v-progress-linear__background {
    opacity: 0 !important;
}

.toggle-action {
    box-shadow: -12.8028px -12.8028px 38.4084px #FFFFFF, 19.2042px 19.2042px 38.4084px rgba(174, 174, 192, 0.4);
    /* transform: matrix(-1, 0, 0, 1, 0, 0); */
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.v-input--selection-controls__ripple.primary--text {
    color: #C41E61 !important;
}

.toggle-title {

    font-weight: 900;
    font-size: 1.3rem !important;
    text-align: right;
}
.active-circle {
    display: inline-block;
    background: #C41E61;
    height: .6rem;
    width: .6rem;
    border-radius: 50%;
}
.meets--text.active {
    background: #75FF04;
}

.toggle-action-icon {
    width: 20px;
    display: block;
    border-radius: 50%;
    height: 20px;
    background: #E20074;
    box-shadow: 19.2042px 19.2042px 38.4084px rgba(174, 174, 192, 0.4), inset 12.8028px 12.8028px 25.6056px rgba(113, 3, 59, 0.33), inset -12.8028px -12.8028px 12.8028px rgba(255, 2, 132, 0.7);
    transform: matrix(1, 0, 0, -1, 0, 0);
}
.toggle-action-icon.off {
    background: rgba(221, 218, 220, 0.5);
    box-shadow: 24.1621px 24.1621px 48.3241px rgba(174, 174, 192, 0.4), -16.108px -16.108px 48.3241px #FFFFFF, inset 16.108px 16.108px 32.2161px rgba(136, 136, 136, 0.33), inset -16.108px -16.108px 16.108px rgba(252, 252, 252, 0.7);
    transform: matrix(1, 0, 0, -1, 0, 0);
}

    .card-value {
        font-size: 3.9rem !important;
        font-weight: 800;
        text-align: center;
    }
    .card-name {
        font-weight: 700 !important;
        font-size: 1.2rem !important;
    }

    .harvest-value h1{
        font-size: 8.2rem !important;
        font-weight: 700;
        line-height: 8rem;
        /* background: -webkit-linear-gradient(180.03deg, #E20074 -28.53%, #C200E2 99.97%) !important; */
        background: -webkit-linear-gradient(#E20074, #C200E2);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;

         /* background: -webkit-linear-gradient(#eee, #333);
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent; */
    }

    .harvest-card {
        background: #F0F0F3 !important;
        box-shadow: -30.6667px -30.6667px 92px #FFFFFF, 30.6667px 30.6667px 92px rgba(174, 174, 192, 0.4) !important;
        border-radius: 15.8px !important;
        position: relative;
        width: 295px;
        height: 295px;
        overflow: hidden;
    }

    .harvest-box {
        padding-bottom: 15px;
    }

    .harvest-title {
        display: flex;
        justify-content: center;
        font-weight: 700 !important;
        font-size: 1.1em !important;
    }

    hr.harvest-border {
        color: 0.2px solid #C4C4C4;
        display: block;
        width: 58%;
        margin: auto;
        margin-top: -23px;
    }

    #latency-value, #uplink-value {
        font-size: 3.2rem !important;
        line-height: 2rem !important;
        font-weight: 800 !important;
        margin-top: 7px !important;
        margin-bottom: -5px !important;
    }
    .live-feed-box {
        padding-bottom: 60px;
    }
    .live-feed {
        font-weight: 800;
        color: rgba(0, 0, 0, 0.8) !important;
    }
    #live-feed-video {
        height: 100%;
        width: 100%;
        display: block;
        min-height: 350px;
        margin-top: 20px;
    }

    /* // meter styling */
    #blocks-meter-bar {
        height: 25px;
        width: 100%;
        color: #fff;
    }

    #blocks-meter-bar > * {
        padding-top: 0.5%;
        flex: 1;
        margin-left: 2px;
        text-align: center;
        font-size: .9rem !important;
        font-weight: 700;
    }

    #blocks-meter-bar:first-child {
        margin-left: 0;
    }

    .square-meter-value.one {
        background: #EB5757;
        border-radius: 10px 0px 0px 10px;
    }
    .square-meter-value.two {
        background: #F29A48;
    }
    .square-meter-value.three {
        background: #F1C94F;
    }
    .square-meter-value.four {
        background: #A0C23A;
